@import "../variables";
.portfolio-part {
  background-color: #ffffff;
  position: absolute;
  //right: 100%;
  width: 100%;
  top: 0;
  z-index: 1;
  //display: none;

  .portfolio-info-header{
    text-align: center;
  }
}

.heading-part-portfolio {
  text-align: center;

  .heading-part {
    margin-bottom: 50px;
    display: inline-block;
    position: relative;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }
}

.portfolio-gallery {
  display: inline-block;
  width: 100%;

  a {
    display: inline-block;
    width: 100%;
    margin-bottom: 30px;
    text-align: center;
  }

  .portfolio-img {
    display: inline-block;
    width: auto;
    text-align: center;
    position: relative;
    overflow: hidden;
    z-index: 1;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    border: 1px solid #80808030;

    img {
      display: inline-block;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }

    .portfolio-text {
      position: absolute;
      bottom: -111px;
      left: 0px;
      width: 100%;
      text-align: left;
      padding: 30px;
      z-index: 1;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;

      h4 {
        font-size: 18px;
        text-transform: uppercase;
        color: #000000;
        margin-top: 0;
      }

      p {
        margin: 0;
        text-transform: capitalize;
        color: #000000;
      }
    }
  }
}
.portfolio-img:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5);
  z-index: 0;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.portfolio-img:hover:after {
  opacity: 1;
  visibility: visible;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.portfolio-img:hover img {
  display: inline-block;
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.portfolio-img:hover .portfolio-text {
  bottom: 0;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.portfolio-part{
  background-color: #ffffff;
  position: absolute;
  width: 100%;
  z-index: 1;
  -webkit-animation-duration: 2s;
  -moz-animation-duration: 2s;
  -ms-animation-duration: 2s;
  -o-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-delay: 200ms;
  -moz-animation-delay: 200ms;
  -ms-animation-delay: 200ms;
  -o-animation-delay: 200ms;
  animation-delay: 200ms;
}
#tech-portfolio{
  margin-left: 5px;
}
.portfolio-gallery a.tech-link{
  width: auto;
  margin: 0;
}
@-webkit-keyframes showPortfolioPage {
  from { right: 100%; }
  to { right: 0; }
}

@-webkit-keyframes hidePortfolioPage {
  from { right: 0; opacity: 1; }
  to { right: 100%; opacity: 0; }
}

@keyframes showPortfolioPage {
  from { right: 100%; }
  to { right: 0; }
}

@keyframes hidePortfolioPage {
  from { right: 0; opacity: 1; }
  to { right: 100%; opacity: 0; }
}

/* MEDIA */
@media (min-width: 320px) and (max-width: 575px) {
  div.portfolio-img{
    margin-left: 10px;
    margin-right: 10px;
  }
}
