@import "../variables";
.skills-part{
    background-color: #ffffff;
    position: absolute;
    width: 100%;
    //top: 100%;
    z-index: 1;
    //display: none;
}

/* Slider */
.skills-slider{
  display: inline-block;
  width: 100%;

  .heading-part-skills{
    text-align: center;

    .heading-part {
      margin-bottom: 50px;
      display: inline-block;
      position: relative;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }
  }
}

.services-slider-images{
  display: inline-block;
  width: 100%;
  text-align:center;
  position:relative;
  z-index:1;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.services-slider-images{
  img.img-1, img.img-2, img.img-3, img.img-4{
    position: absolute;
    left: 0;
    top: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }
}
.services-slider-images{
  .img-2 {
    z-index: 3;
    -webkit-transform: translateX(-44px) scale(0.9);
    -moz-transform: translateX(-44px) scale(0.9);
    -ms-transform: translateX(-44px) scale(0.9);
    -o-transform: translateX(-44px) scale(0.9);
    transform: translateX(-44px) scale(0.9);
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }

  .img-3 {
    z-index: 2;
    -webkit-transform: translateX(-85px) scale(0.8);
    -moz-transform: translateX(-85px) scale(0.8);
    -ms-transform: translateX(-85px) scale(0.8);
    -o-transform: translateX(-85px) scale(0.8);
    transform: translateX(-85px) scale(0.8);
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }

  .img-4 {
    z-index: 1;
    -webkit-transform: translateX(-125px) scale(0.7);
    -moz-transform: translateX(-125px) scale(0.7);
    -ms-transform: translateX(-125px) scale(0.7);
    -o-transform: translateX(-125px) scale(0.7);
    transform: translateX(-125px) scale(0.7);
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }

  img.active {
    -webkit-transform: translateX(0) scale(1);
    -moz-transform: translateX(0) scale(1);
    -ms-transform: translateX(0) scale(1);
    -o-transform: translateX(0) scale(1);
    transform: translateX(0) scale(1);
    z-index: 4;
    -webkit-animation:fadeIn 0.5s;
    -moz-animation:fadeIn 0.5s;
    -ms-animation:fadeIn 0.5s;
    -o-animation:fadeIn 0.5s;
    animation:fadeIn 0.5s;
    height: 390px;
  }
}

.services-slider-box {
  display: inline-block;
  width: 100%;

  .services-slider-text {
    float: left;
    width: 50%;
    text-align: center;
    overflow: hidden;
    height: 210px;
    margin-bottom: 30px;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;

    img {
      display: inline-block;
      width: 70px;
      height: 70px;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }

    .icon-secondary img {
      width: 35px;
      height: 35px;
    }

    h4 {
      text-transform: capitalize;
      font-size: 18px;
      margin-bottom: 0;
      margin-top: 20px;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }

    p {
      margin-bottom: 0;
      margin-top: 20px;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }
  }
}
.icon-main,.icon-secondary{
  background-image: url("../../assets/images/skills/circle-5.png");
  background-size: 55px 55px;
  background-repeat: no-repeat;
  background-position: center;
}
.icon-secondary{
  background-size: 35px 35px;
}

.services-slider-text:hover img, .services-slider-text.active img{
  width:86px;
  height:115px;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

/* Slider END*/
/* Separator */
.separator-part {
  display: inline-block;
  width: 100%;
  background-image: url("../../assets/images/content/counter_bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  position: relative;
  z-index: 1;

  .separator-box {
    display: inline-block;
    width: 100%;
    text-align: left;
    padding-left: 100px;
    margin-bottom: 30px;

    span {
      font-size: 30px;
      color: #c68ffc;
      border: 2px solid #887dfa;
      padding: 15px;
      border-radius: 50%;
    }
    span.separator-js{
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}
.separator-part:after {
  content: "";
  position: absolute;
  background-color: rgba(0, 0, 0, 0.85);
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  z-index: -1;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

/* Separator END*/
/* My Skills */
.my-skills-part{
  display: inline-block;
  width: 100%;

  .heading-part-my-skills{
    text-align: center;

    .heading-part {
      margin-bottom: 50px;
      display: inline-block;
      position: relative;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }
  }
}
/* Canvas */
#canvas {
  display: inline-block;
  width: 100%;

  h5 {
    font-size: 20px;
    text-align: center;
    font-weight: 500;
  }
}

.single-chart {
  width: 90%;
  justify-content: space-around;
  margin: 0 auto;
}

.circular-chart {
  display: block;
  margin: 10px auto;
  max-width: 80%;
  max-height: 250px;
  background-image: url("../../assets/images/content/counter_bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 210px 210px;
  border-radius: 50%;
}

.circle-bg {
  fill: none;
  stroke: #202020;
  stroke-width: 2.2;
}

.circle {
  fill: none;
  stroke-width: 1.7;
  stroke-linecap: round;
  animation: progress 6s ease-out forwards;
}

@keyframes progress {
  0% {
    stroke-dasharray: 0 100;
  }
}

.circular-chart.orange .circle {
  stroke: #ffeb00;
}

.circular-chart.green .circle {
  stroke: #00ff8d;
}

.circular-chart.blue .circle {
  stroke: #0094ff;
}

.circular-chart.darkBlue .circle {
  stroke: #001cff;
}

.percentage {
  fill: #404040;
  font-family: sans-serif;
  font-size: 0.5em;
  text-anchor: middle;
}

/* My Skills END*/
.skills-part{
  background-color: #ffffff;
  position: absolute;
  width: 100%;
  z-index: 1;
  -webkit-animation-duration: 2s;
  -moz-animation-duration: 2s;
  -ms-animation-duration: 2s;
  -o-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-delay: 200ms;
  -moz-animation-delay: 200ms;
  -ms-animation-delay: 200ms;
  -o-animation-delay: 200ms;
  animation-delay: 200ms;
}
@-webkit-keyframes showSkillsPage {
  from { top: 100%; }
  to { top: 0; }
}

@-webkit-keyframes hideSkillsPage {
  from { top: 0; opacity: 1; }
  to { top: 100%; opacity: 0; }
}

@keyframes showSkillsPage {
  from { top: 100%; }
  to { top: 0; }
}

@keyframes hideSkillsPage {
  from { top: 0; opacity: 1; }
  to { top: 100%; opacity: 0; }
}

/* MEDIA */
@media (min-width: 767px) and (max-width: 999px) {
  .separator-part .separator-box{
    padding-left: 40px;
  }
}
@media (min-width: 480px) and (max-width: 767px) {
  div.my-skills-part{
    padding-bottom: 0;
  }
  .services-slider-images{
    height: 430px;
  }
  .separator-part .separator-box{
    padding-left: 0;
    margin-bottom: 0;
    span{
      font-size: 15px;

    }

    span.separator-js {
      padding-left: 25px;
      padding-right: 25px;
    }
  }
  .circular-chart {
    max-height: 190px;
    max-width: 30%;
  }
}

@media (min-width: 320px) and (max-width: 479px) {
  .services-slider-images{
    height: 430px;
  }
  .separator-part .separator-box{
    padding-left: 0;
    margin-bottom: 0;

    span{
      font-size: 15px;
    }
    span.separator-js{
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  .circular-chart {
    max-height: 190px;
    max-width: 30%;
  }
}