.home-part{
    background-image: url("../../assets/images/banner_bg.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    position: fixed;
    overflow-y: auto;
    padding: 200px 0;
    height: 100%;
    width: 100%;
}
/* Menu */
.menu-part {
  text-align: center;

  a {
    position: absolute;
    text-transform: uppercase;
    color: #202020;
    font-size: 18px;
    margin: 0px auto;
    z-index: 1;
    font-weight: 500;
    letter-spacing: 5px;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
    font-weight: 700;
  }

  a.prt_top {
    top: 45px;
    left: 0;
    right: 0;
    width: 100px;
  }
  a.prt_right {
    top: 50%;
    right: 0;
    transform: rotateZ(90deg);
    margin-top: -15px;
  }
  a.prt_bottom {
    bottom: 45px;
    left: 0;
    right: 0;
    width: 150px;
  }
  a.prt_left {
    top: 50%;
    left: -15px;
    transform: rotateZ(-90deg);
    margin-top: -15px;
  }
}
/* Menu END*/
/* Content */
.home-part h1 {
  position: absolute;
  top: 50%;
  font-size: 130px;
  color: #ffffff;
  font-weight: bold;
  //background: url(/images/bg-fon-2.jpg) no-repeat;
  background: url("../../assets/images/fon__6.jpg") no-repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: cover;
}
/* Content END*/