@import "../variables";
.contact-part{
    background-color: #ffffff;
    position: absolute;
    //left: 100%;
    width: 100%;
    top: 0;
    z-index: 1;
    //display: none;

}

.contact-info{
    display: inline-block;
    margin-bottom: 50px;
    width: 100%;

    .contact-info-header{
        text-align: center;
    }

    h1 {
        font-size: 30px;
        display: inline-block;
        margin: 0px auto 35px auto;
        text-transform: uppercase;
    }
}
/* Email */
#email{
    width: 80%;
    margin-left: auto;
    margin-right: auto;
}

.contact-form{
    display: inline-block;
    width: 100%;

    .form-info-row{
        margin-right: 0;
        margin-left: 0;
    }

    .form-info-btn{
        height: 50px;
        line-height: 50px;
        color: #ffffff;
        font-size: 15px;
        font-weight: 400;
        border-radius: 0px;
        padding: 0 20px;
        min-width: 140px;
        outline: none;
        cursor: pointer;
        display: inline-block;
        text-align: center;
        text-transform: capitalize;
        background-color: $main-color;
        border: 1px solid #390066;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -ms-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
    }

    .form-info-btn:hover, .form-info-btn:focus {
        color: #5c009f;
        background-color: #ffffff;
        border: 1px solid $main-color;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -ms-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
    }
    .invalid-feedback{
        display: block!important;
    }
}
/* Email END */
/* Details */
.contact-details {
    display: inline-block;
    width: 100%;

    .contact-details-box {
        padding: 80px 0;
        text-align: center;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        z-index: 1;
        margin-top: 50px;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -ms-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;

        h4 {
            font-size: 18px;
            margin-top: 0;
            margin-bottom: 20px;
            text-transform: capitalize;
            color: #ffffff;
        }

        p {
            margin: 0;
        }
    }

    .contact-details-box:hover {
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -ms-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
        -webkit-transform: scale(1.05);
        -moz-transform: scale(1.05);
        -o-transform: scale(1.05);
        -ms-transform: scale(1.05);
        transform: scale(1.05);
    }

    .contact-details-box:after {
        content: "";
        position: absolute;
        background-color: rgba(0, 0, 0, 0.85);
        top: 0px;
        left: 0px;
        bottom: 0px;
        right: 0px;
        z-index: -1;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -ms-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
    }
    .details-box-1{
        background-image: url("../../assets/images/content/box1.jpg");
    }
    .details-box-2{
        background-image: url("../../assets/images/content/box2.jpg");
    }
    .details-box-3{
        background-image: url("../../assets/images/content/box3.jpg");
    }
}
/* Details END*/
.contact-part{
    background-color: #ffffff;
    position: absolute;
    width: 100%;
    z-index: 1;
    -webkit-animation-duration: 2s;
    -moz-animation-duration: 2s;
    -ms-animation-duration: 2s;
    -o-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-delay: 200ms;
    -moz-animation-delay: 200ms;
    -ms-animation-delay: 200ms;
    -o-animation-delay: 200ms;
    animation-delay: 200ms;
}
@-webkit-keyframes showContactPage {
    from { left: 100%; }
    to { left: 0; }
}

@-webkit-keyframes hideContactPage {
    from { left: 0; opacity: 1; }
    to { left: 100%; opacity: 0; }
}

@keyframes showContactPage {
    from { left: 100%; }
    to { left: 0; }
}

@keyframes hideContactPage {
    from { left: 0; opacity: 1; }
    to { left: 100%; opacity: 0; }
}
/* Media */
@media (max-width: 575px) {
    .contact-details-box{
        margin-right: 15px;
        margin-left: 15px;
    }
}