@import "../variables";
/* Footer */
.footer{
  background-color: #f5f5f5;
  margin-top: 115px;
  p {
    display: inline-block;
    width: auto;
    text-align: left;
    color: #202020;
    margin: 0;
    padding-top: 5px;

    a {
      color: $main-color;
      font-weight: 600;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }
  }
  ul {
    float: right;
    width: auto;
    margin: 0;
    padding: 0;

    li:first-child {
      margin-left: 0;
    }

    li {
      display: inline-block;
      width: auto;
      list-style: none;
      margin: 0 5px;
    }

    li a {
      display: inline-block;
      width: 30px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      color: #1c1c1c;
      border-radius: 100%;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }

    li a:hover {
      color: #ffffff;
      background-color: $main-color;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }
  }
}
/* Footer END*/