@import "../variables";
.about-part{
    background-color: #ffffff;
    position: absolute;
    width: 100%;
    z-index: 1;
    -webkit-animation-duration: 2s;
    -moz-animation-duration: 2s;
    -ms-animation-duration: 2s;
    -o-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-delay: 200ms;
    -moz-animation-delay: 200ms;
    -ms-animation-delay: 200ms;
    -o-animation-delay: 200ms;
    animation-delay: 200ms;
}

.arrow-close{
  img {
    position: fixed;
    right: 50px;
    top: 50px;
    cursor: pointer;
    z-index: 2;
  }
}

@-webkit-keyframes showAboutPage {
  from { top: -100%; }
  to { top: 0; }
}

@-webkit-keyframes hideAboutPage {
  from { top: 0; opacity: 1; }
  to { top: -100%; opacity: 0; }
}

@keyframes showAboutPage {
  from { top: -100%; }
  to { top: 0; }
}

@keyframes hideAboutPage {
  from { top: 0; opacity: 1; }
  to { top: -100%; opacity: 0; }
}
/* Info */
.about-info{
  width: 100%;
  display: flex;
}
.about-img{
  display: inline-block;
  width: 100%;
  text-align: center;
  img {
    display: inline-block;
  }
}
.skills-link{
  color: $main-color;
  text-decoration: underline;
}
.about-heading-2 {
  text-align: left;
  .heading-part {
    margin-bottom: 30px;
    display: inline-block;
    position: relative;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;

    h1 {
      font-size: 40px;
      margin: 0;
      text-transform: uppercase;
      position: relative;
      width: auto;
      font-weight: 600;
      padding-bottom: 15px;
    }
    h1:after {
      content: "";
      position: absolute;
      background-color: #202020;
      width: 100%;
      height: 2px;
      top: 100%;
      left: 0;
    }
    p {
      text-align: right;
      width: auto;
      text-transform: uppercase;
      background-color: #ffffff;
      margin: 0;
      padding-left: 10px;
      position: absolute;
      right: 0;
      bottom: -10px;
      color: $main-color;
      font-weight: 600;
    }
  }
}
.about-details{
  display: inline-block;
  width: 100%;
  a.btn-part {
    margin-right: 20px;
    margin-bottom: 10px;
  }
  p{
    color: #717171;
    margin-bottom: 30px;
  }
}
/* Info END*/
/* Education */
.about-education{
  display: inline-block;
  width: 100%;
  .heading-part-content{
    text-align: center;
    .heading-part {
      margin-bottom: 50px;
      display: inline-block;
      position: relative;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;

    }
  }
}

.about-learn-section {
  display: inline-block;
  width: 100%;
  position: relative;
  z-index: 1;
}
.about-learn-section:after {
  content: "";
  position: absolute;
  background-color: #202020;
  width: 2px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0px auto;
  z-index: -1;
}

.about-learn-right {
  display: table;
  width: 100%;
  margin-bottom: 30px;
  position: relative;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;

  .about-learn-year {
    display: table-cell;
    width: 200px;
    vertical-align: middle;

    h1 {
      font-size: 60px;
      margin: 0;
      color: #202020;
      font-weight: 500;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }
  }

  .about-learn-info {
    display: table-cell;
    width: calc(100% - 200px);

    h4 {
      font-size: 18px;
      text-transform: capitalize;
      margin-top: 0px;
    }

    span {
      display: inline-block;
      width: 100%;
      color: $main-color;
      text-transform: capitalize;
      margin-bottom: 10px;
    }

    p {
      margin: 0;
      color: #717171;
    }
  }
}

.about-learn-right:after {
  content: "";
  position: absolute;
  background-color: #000000;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  border: 3px solid #ffffff;
  top: 50%;
  left: -22px;
  margin-top: -7px;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.about-learn-right:hover .about-learn-year h1 {
  color: $main-color;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.about-learn-right:hover:after{
  background-color: #ffffff;
  border: 3px solid $main-color;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.about-learn-right:hover .about-learn-year h1 {
  -webkit-animation-duration: 2s;
  -moz-animation-duration: 2s;
  -ms-animation-duration: 2s;
  -o-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-delay: 300ms;
  -moz-animation-delay: 300ms;
  -ms-animation-delay: 300ms;
  -o-animation-delay: 300ms;
  animation-delay: 300ms;
  animation-iteration-count: infinite;
  animation-name: pulse;
}

.about-learn-left {
  display: table;
  width: 100%;
  margin-bottom: 30px;
  text-align: right;
  position: relative;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.about-learn-left:after {
  content: "";
  position: absolute;
  background-color: #000000;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  border: 3px solid #ffffff;
  top: 50%;
  right: -22px;
  margin-top: -7px;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.about-learn-left .about-learn-year{
  float:right;
  padding-top: 30px;
}

.about-learn-year {
  display: table-cell;
  width: 200px;
  vertical-align: middle;

  h1 {
    font-size: 60px;
    margin: 0;
    color: #202020;
    font-weight: 500;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }
}
.about-learn-left .about-learn-info{
  float:left;
}
.about-learn-info{
  display: table-cell;
  width: calc(100% - 200px);

  h4 {
    font-size: 18px;
    text-transform: capitalize;
    margin-top: 0;
  }
  span {
    display: inline-block;
    width: 100%;
    color: $main-color;
    text-transform: capitalize;
    margin-bottom: 10px;
  }

  p {
    margin: 0;
    color: #717171;
  }
}
.about-learn-left:hover:after {
  background-color: #ffffff;
  border: 3px solid $main-color;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.about-learn-left:hover .about-learn-year h1{
  color: $main-color;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.about-learn-left:hover .about-learn-year h1{
  -webkit-animation-duration: 2s;
  -moz-animation-duration: 2s;
  -ms-animation-duration: 2s;
  -o-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-delay: 300ms;
  -moz-animation-delay: 300ms;
  -ms-animation-delay: 300ms;
  -o-animation-delay: 300ms;
  animation-delay: 300ms;
  animation-iteration-count: infinite;
  animation-name: pulse;
}
@keyframes pulse {
  from {
    transform: scale3d(1, 1, 1);
  }

  50% {
    transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}
/* Experience */
.about-experience {
  display: inline-block;
  width: 100%;

  .heading-part-content {
    text-align: center;

    .heading-part {
      margin-bottom: 50px;
      display: inline-block;
      position: relative;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }
  }

  .about-experience-section {
    display: inline-block;
    width: 100%;
  }

  .about-experience-box {
    display: table;
    width: 100%;
    margin-bottom: 50px;
    position: relative;
    z-index: 1;
    padding: 50px 10px 50px 0px;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }

  .about-experience-box:after {
    content: "";
    position: absolute;
    background-color: #f5f5f5;
    top: 0;
    right: 0;
    bottom: 0;
    width: 69%;
    z-index: -1;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }

  .about-experience-year {
    display: table-cell;
    width: 230px;
    vertical-align: middle;
    h1 {
      font-size: 100px;
      margin: 0;
      color: #202020;
      font-weight: 500;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      -ms-transition: all 0.3s;
      -o-transition: all 0.3s;
      transition: all 0.3s;
    }

    h4 {
      font-size: 18px;
      text-transform: capitalize;
      margin: 0;
      text-align: right;
      color: #717171;
    }
  }

  .about-experience-info {
    display: table-cell;
    width: calc(100% - 230px);
    padding-left: 30px;

    h4 {
      font-size: 18px;
      text-transform: capitalize;
      margin-top: 0;
    }

    span {
      display: inline-block;
      width: 100%;
      color: #202020;
      text-transform: capitalize;
      margin-bottom: 10px;
    }

    p {
      margin: 0;
      color: #717171;
    }
  }

}

.about-experience-box:hover:after{
  background-color:rgba(255, 255, 255, 0.2);
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.about-experience-box:hover .about-experience-year h1{
  color: $main-color;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
/* Experience END*/
/* MEDIA */
@media (max-width: 991px) and (min-width: 768px){
  .about-learn-section:after {
    display: none;
  }

  .about-learn-right,.about-learn-left {
    border-bottom: 1px solid #202020;
    padding-bottom: 30px;
  }
  .home-part h1 {
    font-size: 120px;
    margin: 0;
  }
}

@media (min-width: 480px) and (max-width: 768px) {
  .home-part h1 {
    font-size: 90px;
    margin: 0;
    text-align: center;
  }
  .about-learn-section:after {
    display: none;
  }
  .about-learn-right,.about-learn-left {
    border-bottom: 1px solid #202020;
    padding-bottom: 30px;
  }
  .about-learn-right:after {
    top: 0;
    left: 0;
    margin-top: 0
  }
  .about-learn-left:after {
    top: 0;
    right: 0;
    margin-top: 0
  }
  .about-learn-section:after{
    display:none;
  }
}
@media (max-width: 479px) {
  .home-part h1 {
    font-size: 50px;
    margin: 0;
    text-align: center;
  }
  .about-learn-section:after {
    display: none;
  }
  .about-learn-right:after,
  .about-learn-left:after {
    display:none;
  }
  .about-learn-left .about-learn-year,
  .about-learn-right .about-learn-year{
    display: table-caption;
    width: 100%;
    vertical-align: middle;
    padding-top: 0;
  }
  .about-learn-left .about-learn-info,
  .about-learn-right .about-learn-info{
    width: 100%;
  }
  .about-learn-right,.about-learn-left {
    border-bottom: 1px solid #202020;
    padding-bottom: 30px;
  }

  .about-experience .about-experience-year {
    display: inline-block;
    width: 100%;

    h4 {
      margin-bottom: 50px;
      text-align: left;
    }
  }

  .about-experience .about-experience-info {
    display: inline-block;
    width: 100%;
    padding-left: 0;
  }
}

/* Education END*/